import Vue from 'vue'
export default {

	data(){
		return {
			top : 100
		}
	},
	methods:{
		gotoTop(){
			console.log('click go to top');
			this.top = 0;
		},
		scroll(e){
			//e.target.scrollTop 获取内容 距离定点的距离 不去修改top值，返回顶部会只生效一次。
			// console.log(e.target.scrollTop);
			this.top = e.target.scrollTop
		}
	},
	onLoad:function(){
	},
	onReady:function(e){
	}
}
